export default {
  data () {
    return {
      list:''
    };
  },
  components: {},
  computed: {},
  methods: {},
  onLoad(e){
    this.$store.dispatch('getNewsDetail',{
      id: e.id
    }).then(res=>{
      this.list = res.data
    })
  }
}

